function showUploadNoti() {
  message.loading("", 0);
}

function uploadError(description = "") {
  message.error(description, 3);
}

function hideUploadNoti() {
  message.destroy();
  message.success("");
}

function writeToEditor({ content, image }) {
  const isContainImgName = window.localStorage.getItem(IS_CONTAIN_IMG_NAME) === "true";
  let text = "";
  if (isContainImgName) {
    text = `![${image.filename}](${image.url})`;
  } else {
    text = `![](${image.url})`;
  }
  const markdownEditor = content.markdownEditor;
  const cursor = markdownEditor.getCursor();
  markdownEditor.replaceSelection(`\n${text}\n`, cursor);
  content.setContent(markdownEditor.getValue());
}

// 
async function compressImage(file, quality = 0.8, maxWidth = 1920) {
  return new Promise((resolve, reject) => {
    // 
    if (!file.type.startsWith('image/')) {
      resolve(file);
      return;
    }

    //  200KB
    if (file.size < 200 * 1024) {
      console.log(`(${(file.size / 1024).toFixed(2)}KB)`);
      resolve(file);
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(file);

    reader.onload = (e) => {
      const img = new Image();
      img.src = e.target.result;

      img.onload = () => {
        const canvas = document.createElement('canvas');
        let width = img.width;
        let height = img.height;

        // 
        if (width > maxWidth) {
          height = (maxWidth / width) * height;
          width = maxWidth;
        }

        canvas.width = width;
        canvas.height = height;

        const ctx = canvas.getContext('2d');
        ctx.drawImage(img, 0, 0, width, height);

        //  PNG JPEG 
        let outputType = file.type;
        let outputQuality = quality;

        if (file.type === 'image/png') {
          // 
          const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
          const data = imageData.data;
          let hasAlpha = false;

          for (let i = 3; i < data.length; i += 4) {
            if (data[i] < 255) {
              hasAlpha = true;
              break;
            }
          }

          //  JPEG
          if (!hasAlpha) {
            outputType = 'image/jpeg';
            outputQuality = 0.85; // JPEG 
          }
        }

        //  Blob
        canvas.toBlob(
          (blob) => {
            if (blob) {
              // 
              if (blob.size >= file.size) {
                // 
                console.log(`(${(file.size / 1024).toFixed(2)}KB -> ${(blob.size / 1024).toFixed(2)}KB)`);
                resolve(file);
              } else {
                //  File 
                const compressedFile = new File([blob], file.name, {
                  type: outputType,
                  lastModified: Date.now(),
                });

                // 
                const reduction = ((1 - blob.size / file.size) * 100).toFixed(1);
                console.log(`: ${(file.size / 1024).toFixed(2)}KB -> ${(blob.size / 1024).toFixed(2)}KB (${reduction}%)`);
                resolve(compressedFile);
              }
            } else {
              reject(new Error(''));
            }
          },
          outputType,
          outputQuality
        );
      };

      img.onerror = () => {
        reject(new Error(''));
      };
    };

    reader.onerror = () => {
      reject(new Error(''));
    };
  });
}


// 
export const qiniuOSSUpload = async ({
  file = {},
  onSuccess = () => { },
  onError = () => { },
  onProgress = () => { },
  images = [],
  content = null, // store content
}) => {
  showUploadNoti();
  const config = JSON.parse(window.localStorage.getItem(QINIUOSS_IMAGE_HOSTING));
  try {
    let { domain } = config;
    const { namespace } = config;
    // domain/
    if (domain[domain.length - 1] !== "/") {
      domain += "/";
    }
    const result = await axiosMdnice.get(`/ qiniu / ${config.bucket} /${config.accessKey}/${config.secretKey} `);
    const token = result.data;

    const base64Reader = new FileReader();

    base64Reader.readAsDataURL(file);

    base64Reader.onload = (e) => {
      const urlData = e.target.result;
      const base64 = urlData.split(",").pop();
      const fileType = urlData
        .split(";")
        .shift()
        .split(":")
        .pop();

      // base64blob
      const blob = toBlob(base64, fileType);

      const conf = {
        useCdnDomain: true,
        region: qiniu.region[config.region], // 
      };

      const putExtra = {
        fname: "",
        params: {},
        mimeType: [] || null,
      };

      const OSSName = getOSSName(file.name, namespace);

      // blob
      const imageObservable = qiniu.upload(blob, OSSName, token, putExtra, conf);

      // 
      const complete = (response) => {
        // console.log(response);
        const names = file.name.split(".");
        names.pop();
        const filename = names.join(".");
        const image = {
          filename, // 
          url: encodeURI(`${domain}${response.key} `),
        };
        images.push(image);

        if (content) {
          writeToEditor({ content, image });
        }
        onSuccess(response);
        setTimeout(() => {
          hideUploadNoti();
        }, 500);
      };

      // 
      const next = (response) => {
        // console.log(response);
        const percent = parseInt(Math.round(response.total.percent.toFixed(2)), 10);
        onProgress(
          {
            percent,
          },
          file,
        );
      };

      // 
      const error = (err) => {
        hideUploadNoti();
        uploadError();
        onError(err, err.toString());
      };

      const imageObserver = {
        next,
        error,
        complete,
      };
      //  imageObserver 
      imageObservable.subscribe(imageObserver);
    };
  } catch (err) {
    onError(err, err.toString());
  }
};

// 
export const customImageUpload = async ({
  formData = new FormData(),
  file = {},
  onSuccess = () => { },
  onError = () => { },
  images = [],
  content = null,
}) => {
  showUploadNoti();
  try {
    // 
    const compressedFile = await compressImage(file, 0.8, 1920);

    formData.append("file", compressedFile);
    const config = {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    };
    const postURL = imageHosting.hostingUrl;
    const result = await axios.post(postURL, formData, config);
    const names = file.name.split(".");
    names.pop();
    const filename = names.join(".");
    const image = {
      filename,
      url: encodeURI(result.data.data), // 
    };

    if (content) {
      writeToEditor({ content, image });
    }
    images.push(image);
    onSuccess(result);
    setTimeout(() => {
      hideUploadNoti();
    }, 500);
  } catch (error) {
    message.destroy();
    uploadError(error.toString());
    onError(error, error.toString());
  }
};

// SM.MS
export const smmsUpload = ({
  formData = new FormData(),
  file = {},
  action = SM_MS_PROXY,
  onProgress = () => { },
  onSuccess = () => { },
  onError = () => { },
  headers = {},
  withCredentials = false,
  images = [],
  content = null, // store content
}) => {
  showUploadNoti();
  // SM.MSsmfile
  formData.append("smfile", file);
  axios
    .post(action, formData, {
      withCredentials,
      headers,
      onUploadProgress: ({ total, loaded }) => {
        onProgress(
          {
            percent: parseInt(Math.round((loaded / total) * 100).toFixed(2), 10),
          },
          file,
        );
      },
    })
    .then(({ data: response }) => {
      if (response.code === "exception") {
        throw response.message;
      }
      const image = {
        filename: response.data.filename,
        url: response.data.url,
      };
      if (content) {
        writeToEditor({ content, image });
      }
      images.push(image);
      onSuccess(response, file);
      setTimeout(() => {
        hideUploadNoti();
      }, 500);
    })
    .catch((error) => {
      hideUploadNoti();
      uploadError(error.toString());
      onError(error, error.toString());
    });
};

// 
const aliOSSPutObject = ({ config, file, buffer, onSuccess, onError, images, content }) => {
  let client;
  try {
    client = new OSS(config);
  } catch (error) {
    message.error("OSS");
    return;
  }

  const OSSName = getOSSName(file.name);

  client
    .put(OSSName, buffer)
    .then((response) => {
      const names = file.name.split(".");
      names.pop();
      const filename = names.join(".");
      const image = {
        filename, // 
        url: response.url,
      };
      if (content) {
        writeToEditor({ content, image });
      }
      images.push(image);
      onSuccess(response, file);
      setTimeout(() => {
        hideUploadNoti();
      }, 500);
    })
    .catch((error) => {
      console.log(error);

      hideUploadNoti();
      uploadError("");
      onError(error, error.toString());
    });
};

// 
export const aliOSSUpload = ({
  file = {},
  onSuccess = () => { },
  onError = () => { },
  images = [],
  content = null, // store content
}) => {
  showUploadNoti();
  const config = JSON.parse(window.localStorage.getItem(ALIOSS_IMAGE_HOSTING));
  const base64Reader = new FileReader();
  base64Reader.readAsDataURL(file);
  base64Reader.onload = (e) => {
    const urlData = e.target.result;
    const base64 = urlData.split(",").pop();
    const fileType = urlData
      .split(";")
      .shift()
      .split(":")
      .pop();

    // base64blob
    const blob = toBlob(base64, fileType);

    // blobarrayBuffer
    const bufferReader = new FileReader();
    bufferReader.readAsArrayBuffer(blob);
    bufferReader.onload = (event) => {
      const buffer = new OSS.Buffer(event.target.result);
      aliOSSPutObject({ config, file, buffer, onSuccess, onError, images, content });
    };
  };
};

// Gitee
export const giteeUpload = ({
  formData = new FormData(),
  file = {},
  onProgress = () => { },
  onSuccess = () => { },
  onError = () => { },
  headers = {},
  withCredentials = false,
  images = [],
  content = null, // store content
}) => {
  showUploadNoti();

  if (file.size / 1024 / 1024 > 1) {
    message.warn(" 1 MB");
  }

  const config = JSON.parse(window.localStorage.getItem(GITEE_IMAGE_HOSTING));

  const base64Reader = new FileReader();
  base64Reader.readAsDataURL(file);
  base64Reader.onload = (e) => {
    const urlData = e.target.result;
    const base64 = urlData.split(",").pop();

    const date = new Date();
    const seperator = "-";
    const dir = date.getFullYear() + seperator + (date.getMonth() + 1) + seperator + date.getDate();

    const dateFilename = new Date().getTime() + "-" + file.name;
    const url = `https://gitee.com/api/v5/repos/${config.username}/${config.repo}/contents/${dir}/${dateFilename}`;

    formData.append("content", base64);
    formData.append("access_token", config.token);
    formData.append("message", "mdnice upload picture");

    axios
      .post(url, formData, {
        withCredentials,
        headers,
        onUploadProgress: ({ total, loaded }) => {
          onProgress(
            {
              percent: parseInt(Math.round((loaded / total) * 100).toFixed(2), 10),
            },
            file,
          );
        },
      })
      .then(({ data: response }) => {
        if (response.code === "exception") {
          throw response.message;
        }
        const names = file.name.split(".");
        names.pop();
        const filename = names.join(".");
        const image = {
          filename,
          url: encodeURI(response.content.download_url),
        };
        if (content) {
          writeToEditor({ content, image });
        }
        images.push(image);
        onSuccess(response, file);
        setTimeout(() => {
          hideUploadNoti();
        }, 500);
      })
      .catch((error, info) => {
        hideUploadNoti();
        uploadError(error.toString() + " ");
        onError(error, error.toString() + " ");
      });
  };
};

// AWS S3
export const awsS3Upload = async ({
  file = {},
  onSuccess = () => { },
  onError = () => { },
  images = [],
  content = null, // store content
}) => {
  showUploadNoti();
  const config = JSON.parse(window.localStorage.getItem(AWS_S3_IMAGE_HOSTING));

  try {
    const date = new Date();
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const fullName = file.name;

    let key = config.path || "{year}/{month}/{fullName}";
    key = key.replace("{year}", year).replace("{month}", month).replace("{fullName}", fullName);

    // Remove leading slash if present to avoid empty folder at root
    if (key.startsWith("/")) {
      key = key.substring(1);
    }

    //  URL
    const uploadUrl = `${config.endpoint}/${config.bucket}/${key}`;

    console.log(':', {
      uploadUrl,
      bucket: config.bucket,
      key,
      fileType: file.type,
      fileSize: file.size
    });

    //  XMLHttpRequest 
    const arrayBuffer = await file.arrayBuffer();

    const xhr = new XMLHttpRequest();
    xhr.open('PUT', uploadUrl, true);
    xhr.setRequestHeader('Content-Type', file.type);

    //  AWS  AWS Signature V4
    //  bucket 

    xhr.onload = function () {
      if (xhr.status === 200 || xhr.status === 204) {
        //  URL
        const baseUrl = config.publicUrl.endsWith("/") ? config.publicUrl.slice(0, -1) : config.publicUrl;
        const url = `${baseUrl}/${key}`;

        const image = {
          filename: file.name.replace(/\.[^/.]+$/, ""),
          url: encodeURI(url),
        };

        if (content) {
          writeToEditor({ content, image });
        }
        images.push(image);
        onSuccess(image, file);
        setTimeout(() => {
          hideUploadNoti();
        }, 500);
      } else {
        throw new Error(`: ${xhr.status}, : ${xhr.responseText}`);
      }
    };

    xhr.onerror = function () {
      throw new Error(' CORS ');
    };

    xhr.send(arrayBuffer);

  } catch (error) {
    message.destroy();
    console.error('AWS S3 :', error);
    uploadError(`: ${error.message || error.toString()}`);
    onError(error, error.toString());
  }
};

// GitHub
export const githubUpload = ({
  formData = new FormData(),
  file = {},
  onProgress = () => { },
  onSuccess = () => { },
  onError = () => { },
  headers = {},
  withCredentials = false,
  images = [],
  content = null, // store content
}) => {
  showUploadNoti();

  const config = JSON.parse(window.localStorage.getItem(GITHUB_IMAGE_HOSTING));

  const base64Reader = new FileReader();
  base64Reader.readAsDataURL(file);
  base64Reader.onload = (e) => {
    const urlData = e.target.result;
    const base64 = urlData.split(",").pop();

    const date = new Date();
    const seperator = "-";
    const dir = date.getFullYear() + seperator + (date.getMonth() + 1) + seperator + date.getDate();

    const dateFilename = new Date().getTime() + "-" + file.name;
    const url = `https://api.github.com/repos/${config.username}/${config.repo}/contents/${dir}/${dateFilename}?access_token=${config.token}`;

    const data = {
      content: base64,
      message: "mdnice upload picture",
    };

    axios
      .put(url, data, {
        withCredentials,
        headers,
        onUploadProgress: ({ total, loaded }) => {
          onProgress(
            {
              percent: parseInt(Math.round((loaded / total) * 100).toFixed(2), 10),
            },
            file,
          );
        },
      })
      .then(({ data: response }) => {
        if (response.code === "exception") {
          throw response.message;
        }
        const names = file.name.split(".");
        names.pop();
        const filename = names.join(".");

        const imageUrl =
          config.jsdelivr === "true"
            ? `https://cdn.jsdelivr.net/gh/${config.username}/${config.repo}/${dir}/${dateFilename}`
            : response.content.download_url;

        const image = {
          filename,
          url: encodeURI(imageUrl),
        };
        if (content) {
          writeToEditor({ content, image });
        }
        images.push(image);
        onSuccess(response, file);
        setTimeout(() => {
          hideUploadNoti();
        }, 500);
      })
      .catch((error, info) => {
        hideUploadNoti();
        uploadError(error.toString());
        onError(error, error.toString());
      });
  };
};

// 
export const uploadAdaptor = (...args) => {
  const type = localStorage.getItem(IMAGE_HOSTING_TYPE); // SM.MS |  |  | Gitee | GitHub | 
  const userType = imageHosting.hostingName;
  if (type === userType) {
    return customImageUpload(...args);
  } else if (type === IMAGE_HOSTING_NAMES.smms) {
    return smmsUpload(...args);
  } else if (type === IMAGE_HOSTING_NAMES.qiniuyun) {
    const config = JSON.parse(window.localStorage.getItem(QINIUOSS_IMAGE_HOSTING));
    if (
      !config.region.length ||
      !config.accessKey.length ||
      !config.secretKey.length ||
      !config.bucket.length ||
      !config.domain.length
    ) {
      message.error("");
      return false;
    }
    return qiniuOSSUpload(...args);
  } else if (type === IMAGE_HOSTING_NAMES.aliyun) {
    const config = JSON.parse(window.localStorage.getItem(ALIOSS_IMAGE_HOSTING));
    if (
      !config.region.length ||
      !config.accessKeyId.length ||
      !config.accessKeySecret.length ||
      !config.bucket.length
    ) {
      message.error("");
      return false;
    }
    return aliOSSUpload(...args);
  } else if (type === IMAGE_HOSTING_NAMES.gitee) {
    const config = JSON.parse(window.localStorage.getItem(GITEE_IMAGE_HOSTING));
    if (!config.username.length || !config.repo.length || !config.token.length) {
      message.error(" Gitee ");
      return false;
    }
    return giteeUpload(...args);
  } else if (type === IMAGE_HOSTING_NAMES.github) {
    const config = JSON.parse(window.localStorage.getItem(GITHUB_IMAGE_HOSTING));
    if (!config.username.length || !config.repo.length || !config.token.length) {
      message.error(" GitHub ");
      return false;
    }
    return githubUpload(...args);
  } else if (type === IMAGE_HOSTING_NAMES.aws) {
    const config = JSON.parse(window.localStorage.getItem(AWS_S3_IMAGE_HOSTING));
    if (!config.accessKeyId.length || !config.secretAccessKey.length || !config.bucket.length) {
      message.error(" AWS S3 ");
      return false;
    }
    return awsS3Upload(...args);
  }
  return true;
};
